.main {
  background-image: url('./background.jpg');
  min-height: 100vh;
  min-width: 100vw;
  overflow: hidden;
  background-position: center;
  background-size: cover;
}

.main-top {
  display: flex;
  flex-direction: row;
  padding: 30px;
  margin: auto;
  justify-content: space-between;
}

.main-top-logo {
  margin-left: 0px;
  margin-right: auto;
}

.main-top-connect {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 68px;
}

.main-middle {
  display: flex;
  flex-direction: row;
}

.main-middle-landing {
  margin-left: 60px;
  margin-right: 40px;
}

.main-middle-product {
  position: absolute;
  left: 69vw;
}

.main-button {
  position: absolute;
  left: 1700px;
  top: 990px;
  height: 30px;
  width: 50px;
}

.main-link {
  position: relative;
  top: -40px;
  color: white;
}